.capital_card {
  border: 1px solid rgba(255, 165, 0, 0.25);
  border-radius: 8px;
  padding: 16px;
  width: 86%;
  height: 100%;
  background-color: #2d3a3a3b;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.capital_card h3 {
  margin-top: 0;
  font-size: 18px;
  color: #ffffff;
}

/* === ВАЖНО: контейнер с горизонтальной прокруткой === */
.scroll_container {
  display: flex;
  flex-direction: row;
  overflow-x: auto;
  overflow-y: hidden; /* Убираем вертикальный скролл */
  gap: 16px;
  padding: 10px 0;
  margin-top: 10px;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE и Edge */
}

.scroll_container::-webkit-scrollbar {
  display: none; /* Chrome, Safari */
}

.scroll_container::-webkit-scrollbar-thumb {
  background-color: orange;
  border-radius: 8px;
}

/* === Элементы внутри прокрутки === */
.capital_item {
  
  min-width: 47%; /* Ширина одного блока */
  height: 5em;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 12px 16px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  transition: background-color 0.2s ease;
}

.capital_item:hover {
  background-color: rgba(255, 243, 224, 0.5);
  cursor: pointer;
}

.capital_item .label {
  font-size: 14px;
  color: #ffffff;
  margin-bottom: 4px;
}

.capital_item .value {
  font-size: 16px;
  color: #ffffff;
  font-weight: bold;
}

.capital_item.earned .value {
  color: green;
}