.dashboardLayout {
  display: flex;
  padding-top: 120px;

}

.dashboardBorder {
  width: 182px;
  height: auto;

}

.sidebar {
  position: sticky;
  top: 130px; /* отступ от верха — тот же, что и height у header */
  border: 2px solid white;
  border-radius: 20px;
  padding: 10px;
}

.content {
  flex: 1; /* Занимает оставшуюся ширину */
  padding: 24px 0 32px 24px;
}