/* Technologies.module.css */

/* Корпоративные цвета */
:root {
  --black: #1a1a1a;
  --gold: #ffd700;
  --darkGold: #d4af37;
  --gradient: linear-gradient(135deg, #1a1a1a, #2a2a2a, #ffd700);
}

/* Блок 12 */
.block12 {
  text-align: start;
  margin-bottom: 1vh;
}

.block12Title {
  padding: 5vh 0 0 0;
  font-size: 3.5rem;
  text-transform: uppercase;
  background: linear-gradient(
    90deg,
    var(--white),
    var(--secondary-gold),
    var(--primary-gold)
  );
  -webkit-background-clip: text; /* Для поддержки градиента */
  -webkit-text-fill-color: transparent; /* Для поддержки градиента */
}

/* Блок 13 */
.block13 {
  background: var(--black);
  border-radius: 25px;
  box-shadow:
    0 0.34rem 0.55rem rgba(0, 0, 0, 0.1),
    0 0.13rem 0.21rem rgba(0, 0, 0, 0.08);
  padding: 0.5rem 2rem;
  margin: 3rem auto;
  border: 1px solid var(--secondary-gold);
  max-width: 85vw;
  position: relative;
  z-index: 1;
}

.block13Text {
  font-size: 1.5rem;
  text-align: center;
  background: linear-gradient(
    90deg,
    #fffefa,
    #fff7d5,
    #f0e68c
  ); /* Золотой градиент */
  -webkit-background-clip: text; /* Применяем градиент к тексту */
  background-clip: text;
  color: transparent; /* Скрываем основной цвет текста */
}

/* Контейнер для парных блоков */
.blocksContainer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(21.34rem, 1fr));
  gap: 3.82rem;
  padding: 2rem;
  justify-items: center;
  position: relative;
  z-index: 1;
}

/* Парные блоки */
.block {
  width: 32vw;
  background: var(--black);
  border-radius: 1.34rem;
  box-shadow: 0 0.55rem 1.34rem rgba(0, 0, 0, 0.2);
  padding: 0rem 2rem;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  border: 0.13rem solid rgba(255, 215, 0, 0.1);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.brain_center {
  width: auto;
  height: 6vh;
  background: var(--black);
  border-radius: 1.34rem;
  box-shadow: 0 0.55rem 1.34rem rgba(0, 0, 0, 0.2);
  padding: 0rem 2rem;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  border: 0.13rem solid rgba(255, 215, 0, 0.1);
  position: relative;
  overflow: hidden;
  z-index: 1;

  /* Центрирование содержимого */
  display: flex; /* Используем Flexbox */
  justify-content: center; /* Центрирование по горизонтали */
  align-items: center; /* Центрирование по вертикали */
}

.brain_center_text {
  font-size: 1.5rem;
  text-align: center;
  background: linear-gradient(
    90deg,
    #fffefa,
    #fff7d5,
    #f0e68c
  ); /* Золотой градиент */
  -webkit-background-clip: text; /* Применяем градиент к тексту */
  background-clip: text;
  color: transparent; /* Скрываем основной цвет текста */
}

.block:hover {
  transform: translateY(-0.34rem);
  box-shadow: 0 1.34rem 3.82rem rgba(0, 0, 0, 0.5);
  border-color: rgba(255, 215, 0, 0.5);
}

.block::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    135deg,
    rgba(255, 215, 0, 0.1),
    rgba(255, 215, 0, 0)
  );
  opacity: 0;
  transition: opacity 0.3s;
}

.block:hover::before {
  opacity: 1;
}

.block::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 13.34rem;
  height: 13.34rem;
  background: radial-gradient(
    circle,
    rgba(255, 215, 0, 0.1),
    rgba(255, 215, 0, 0)
  );
  transform: translate(-50%, -50%);
  animation: neuralPulse 4s infinite ease-in-out;
  pointer-events: none;
  z-index: -1;
}

.blockText {
  font-size: 1.13rem;
  line-height: 1.8;
  position: relative;
  background: linear-gradient(
    90deg,
    #fffefa,
    #fff7d5,
    #f0e68c
  ); /* Золотой градиент */
  -webkit-background-clip: text; /* Применяем градиент к тексту */
  background-clip: text;
  color: transparent; /* Скрываем основной цвет текста */
}

.blockTitle {
  color: var(--gold);
  font-size: 1.62rem;
  margin-bottom: 1rem;
  font-weight: 600;
  position: relative;
  z-index: 1;
}

/* Анимации */
@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes neuralPulse {
  0% {
    opacity: 0.3;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
  100% {
    opacity: 0.3;
    transform: scale(1);
  }
}
