.stock-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(135deg, #0a1a2f, #0d0d0d);
  overflow: hidden;
  z-index: -1;
}

.stock-ticker {
  padding: 0 20px;
  position: absolute;
  color: rgba(255, 255, 255, 0.1);
  font-family: monospace;
  white-space: nowrap;
  animation: ticker-animation 40s linear infinite;
}

.ticker span {
  margin: 0 20px;
}

.primary-ticker {
  top: 15%;
  font-size: 14px;
  animation-duration: 30s;
}

.secondary-ticker {
  top: 40%;
  right: 0;
  font-size: 12px;
  animation-duration: 25s;
  animation-direction: reverse;
}

.tertiary-ticker {
  top: 65%;
  font-size: 10px;
  animation-duration: 20s;
}

@keyframes ticker-animation {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}

.volume-stats {
  position: absolute;
  bottom: 20px;
  right: 20px;
  color: rgba(255, 255, 255, 0.1);
  font-family: monospace;
  font-size: 10px;
  animation: fade 5s infinite;
}

@keyframes fade {
  0% {
    opacity: 0.1;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 0.1;
  }
}

/* Стили для японских свечей */
.candle-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; /* Чтобы свечи не мешали взаимодействию */
  overflow: hidden;
}

.candle {
  position: relative;
  opacity: 0.3; /* Почти прозрачные */
  animation: candle-fade 12s infinite;
}

.candle-body {
  position: absolute;
  bottom: 0;
  left: 50%; /* Центрируем тело свечи по горизонтали */
  transform: translateX(-50%); /* Коррекция для точного центрирования */
  width: 8px; /* Ширина тела свечи */
  background-color: rgba(255, 82, 82, 0.3); /* Красный цвет для падения */
  border-radius: 1px;
}

.candle.bullish .candle-body {
  background-color: rgba(46, 125, 50, 0.3); /* Зеленый цвет для роста */
}

.candle-shadow-top,
.candle-shadow-bottom {
  position: absolute;
  left: 50%; /* Центрируем тени по горизонтали */
  transform: translateX(-50%); /* Коррекция для точного центрирования */
  width: 1px; /* Тонкая линия для теней */
  background-color: rgba(255, 255, 255, 0.1); /* Очень слабая видимость */
}

.candle-shadow-top {
  bottom: 100%; /* Начинается над телом свечи */
}

.candle-shadow-bottom {
  top: 100%; /* Начинается под телом свечи */
}

@keyframes candle-fade {
  0% {
    opacity: 0;
    transform: scale(0.5) translateY(100%);
  }
  10% {
    opacity: 0.3;
    transform: scale(1) translateY(0);
  }
  90% {
    opacity: 0.3;
    transform: scale(1) translateY(0);
  }
  100% {
    opacity: 0;
    transform: scale(0.5) translateY(-100%);
  }
}
