.terminal {
  display: flex;
  flex-flow: column nowrap;
  width: 90%;
  justify-content: center;
  position: relative; /* Для позиционирования псевдоэлементов */
  border: 1px solid orange; /* Основная рамка толщиной 1 пиксель */
  border-radius: 30px; /* Закругление углов */
  box-shadow:
    0 0 6px orange,
    /* Внешнее свечение (увеличенная интенсивность) */ 0 0 12px orange; /* Дополнительный внешний слой */
  background: linear-gradient(135deg, #0a1a2f, #0d0d0d);
  padding: 75px 0px 40px 25px;
  margin: 90px auto 0 auto;
}

.graphics_container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;

  
}

.terminal::before {
  content: ""; /* Обязательное свойство для псевдоэлемента */
  position: absolute;
  top: -2px; /* Смещение вверх */
  left: -2px; /* Смещение влево */
  right: -2px; /* Смещение вправо */
  bottom: -2px; /* Смещение вниз */
  border: 1px solid transparent; /* Прозрачная граница */
  border-radius: 30px; /* Закругление углов для псевдоэлемента */
  box-shadow: inset 0 0 12px orange; /* Увеличенное внутреннее свечение */
  pointer-events: none; /* Чтобы псевдоэлемент не мешал взаимодействию */
}
.strategy_section {
  display: flex;

  flex-direction: column;
  justify-content: start;
}

.tools_section {
  display: flex;
  flex-direction: column;
  justify-content: start;
}

.investor_capital {
  display: flex; /* Делаем родительский блок flex-контейнером */
  justify-content: center; /* Центрирование по горизонтали */
  align-items: center; /* Центрирование по вертикали */
  height: 100%; /* Убедитесь, что у родителя есть высота */
  margin: 3em 0 0 0;
}
