.title {
  text-align: start;
}

.title_text {
  padding: 5vh 0 0 0;
  font-size: 3.5rem;
  text-transform: uppercase;
  background: linear-gradient(
    90deg,
    var(--white),
    var(--secondary-gold),
    var(--primary-gold)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.absolute_container {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* Три колонки */
  grid-template-rows: repeat(3, 1fr); /* Три строки */
  gap: 20px; /* Расстояние между элементами */
  justify-items: center; /* Центрирование по горизонтали */
  align-items: center; /* Центрирование по вертикали */
}

.absolute_item {
  width: 15vw;
  height: 20.5vh;
  grid-column: 2 / 3; /* Размещение в центральной колонке */
  grid-row: 2 / 3; /* Размещение в центральной строке */
  cursor: pointer;
  transform-style: preserve-3d;
  transition: transform 0.6s ease-in-out;

  top: 50%; /* Центрирование по вертикали */
  left: 50%; /* Центрирование по горизонтали */
  transform: translate(-50%, -50%); /* Корректировка позиции */
}

.absolute_item .front,
.absolute_item .back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.absolute_item .front {
  background: #222222;
  border: 1px solid #ddd;
}

.absolute_item .back {
  width: 20vw;
  height: auto;
  background: #1a1a1a;
  border: 1px solid #ddd;
  transform: rotateY(180deg);
}

.label {
  font-size: 1.5rem;
  color: #ffffff;
  text-align: center;
}

.description {
  font-size: 1.2rem;
  color: var(--primary-gold);
  text-align: center;
  padding: 20px;
}

.absolute_item.flipped {
  transform: scale(50) rotateY(180deg);
  z-index: 10000;
}

.brainCenter {
  grid-column: 2 / 3; /* Размещение в центральной колонке */
  grid-row: 2 / 3; /* Размещение в центральной строке */
  width: 15vw;
  height: 10vh;
  background: #00534c;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.brainCenter .label {
  font-size: 1.5rem;
  color: #ffffff;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 9999;
  transition: opacity 0.3s;
}
