.block--5-title {
  background: linear-gradient(
    to bottom, #e6d8c3 27%, #fff3d4 40%, #5a4c2f 78%); 
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  padding: 0 0 0 1rem;
  position: relative;
  text-transform: uppercase;  
  font-size: 3.5rem;
  margin: 0;
  font-family: 'Playfair Display';
  font-weight: 800;
}
.block--5-title::after {
  background: none;
  content: attr(data-heading) / "";
  left: 0;
  top: 0;
  z-index: -1;
  position: absolute;
  text-shadow: 
  -1px 0 1px #c6bb9f, 
  0 1px 1px #c6bb9f, 
  5px 5px 10px rgba(0, 0, 0, 0.4),
  -5px -5px 10px rgba(0, 0, 0, 0.4);
}



