/* Заголовок */
.title {
  text-align: start;
  margin-bottom: 10vh;
}

.title_text {
  padding: 5vh 0 0 0;
  font-size: 3.5rem;
  text-transform: uppercase;
  background: linear-gradient(
    90deg,
    var(--white),
    var(--secondary-gold),
    var(--primary-gold)
  );
  -webkit-background-clip: text; /* Для поддержки градиента */
  -webkit-text-fill-color: transparent; /* Для поддержки градиента */
}

/* Общий контейнер */
.main_container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Блоки с текстом */
.broker_block {
  width: 14vw;
  height: 8vh;
  border: 1px solid var(--secondary-gold);
  border-radius: 25px;
  background-color: #222222;
  padding: 10px;
  box-sizing: border-box;
}

.action_block {
  width: 19vw;
  height: 35vh;
  border: 1px solid var(--secondary-gold);
  border-radius: 25px;
  background-color: #222222;
  padding: 10px;
  box-sizing: border-box;
}

.block_text {
  margin: 0;
  padding: 0;
  font-size: 1.5rem; /* Увеличиваем размер шрифта */
  text-align: center; /* Горизонтальное выравнивание */
  display: flex; /* Используем flexbox для центрирования */
  align-items: center; /* Вертикальное выравнивание */
  justify-content: center; /* Горизонтальное выравнивание */
  height: 100%; /* Занимает всю высоту родительского блока */
  background: linear-gradient(
    90deg,
    #fffefa,
    #fff7d5,
    #f0e68c
  ); /* Золотой градиент */
  -webkit-background-clip: text; /* Применяем градиент к тексту */
  background-clip: text;
  color: transparent; /* Скрываем основной цвет текста */
  font-weight: bold; /* Добавляем жирность */
}

/* Контейнеры для групп блоков */
.broker_container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 5vh;
}

.action_container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-bottom: 5vh;
}

/* Контейнер кнопки */
.button_container {
  display: flex;
  justify-content: center;
  margin-bottom: 5vh;
}

/* Стили кнопки */
.invest_button {
  background: linear-gradient(
    90deg,
    var(--secondary-gold),
    var(--primary-gold)
  );
  color: var(--black);
  border: none;
  border-radius: 25px;
  padding: 15px 30px;
  font-size: 1.2rem;
  cursor: pointer;
  transition:
    transform 0.3s ease,
    box-shadow 0.3s ease,
    background 0.3s ease;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden; /* Для эффектов внутри кнопки */
}

/* Эффект при наведении */
.invest_button:hover {
  transform: scale(1.05);
}

/* Эффект при нажатии */
.invest_button:active {
  transform: scale(0.95);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Анимация пульсации */
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 215, 0, 0.7); /* Золотой цвет */
  }
  70% {
    box-shadow: 0 0 0 10px rgba(255, 215, 0, 0); /* Постепенное затухание */
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 215, 0, 0);
  }
}

/* Применение анимации к кнопке */
.invest_button {
  text-transform: uppercase;
  font-size: 1.5rem;
  padding: 2vh 5vw;
  animation: pulse 1.5s infinite; /* Бесконечная пульсация каждые 2 секунды */
}

/* Дополнительный эффект "волны" при наведении */
.invest_button::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 300px;
  height: 300px;
  background: rgba(255, 255, 255, 0.2); /* Полупрозрачный свет */
  border-radius: 50%;
  transform: translate(-50%, -50%) scale(0);
  opacity: 0;
  transition:
    transform 0.5s ease,
    opacity 0.5s ease;
}

.invest_button:hover::after {
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
}

/* Иконки */
.icon_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 20px;
}

.icon_block {
  width: 12vh;
  height: 12vh;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.icon_block:hover {
  transform: scale(1.2);
}

.icon_block:active {
  transform: scale(0.9);
}

.icon_block img {
  width: 60%;
  height: 60%;
}

