.container {
  width: auto;
  position: relative;
  right: 0;
}

.menu_container {
  display: flex;
  position: absolute;
  flex-direction: row;
  width: auto;
  position: absolute;
  z-index: 1;
  left: 0;
  top: -40px;
}

/* Обертка меню */
.menu_wrapper {
  position: relative; /* Для позиционирования выпадающего меню */
  display: inline-block;
  margin: 0 0.5rem 0 0;
}

/* Кнопка открытия меню */
.button {
  width: auto;
  padding: 0.75rem 1.5rem; /* Увеличиваем отступы для более солидного вида */
  font: inherit;
  color: #ffffff; /* Цвет текста */
  background: transparent;
  border: none;
  border-radius: 25px; /* Закругленные углы */
  cursor: pointer;
  transition: all 0.3s ease; /* Плавная анимация */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Мягкая тень */
  font-weight: 500; /* Полужирный шрифт */
  text-transform: uppercase; /* Заглавные буквы */
  letter-spacing: 1px; /* Расстояние между буквами */
}

