.button_text {
  font-size: 0.7rem;
  text-align: center;
}

/* Выпадающее меню */
.menu {
  position: absolute;
  top: 3; /* Размещаем меню под кнопкой */
  left: 0;
  width: 100%; /* Ширина меню равна ширине кнопки */
  background: #2c3e507e; /* Однотонный темный фон */
  border-radius: 10px; /* Закругленные углы */
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2); /* Сильная тень для объема */
  list-style: none;
  padding: 0.5rem 0;
  margin-top: 0.5rem; /* Отступ от кнопки */
  clip-path: inset(0 0 100% 0); /* Начальное состояние: полностью скрыто */
  transition: clip-path 0.5s ease-in-out; /* Плавная анимация */
  z-index: 10; /* Чтобы меню было поверх других элементов */
}

/* Активное состояние меню */
.button.active ~ .menu {
  clip-path: inset(0 0 0 0); /* Полностью видимо */
}

.button.active {
  box-shadow:
    0 6px 8px rgba(0, 0, 0, 0.15),
    /* Усиленная тень */ 0 0 0 2px orange; /* Оранжевая рамка */
  transform: translateY(-2px); /* Легкий эффект "подъема" */
}

/* Элементы меню */
.menu_item {
  overflow: hidden;
  font-size: 0.7rem;
}

/* Ссылки в меню */
.menu_link {
  width: 90%;
  background: none;
  border: none;
  color: #ffffff; /* Белый текст */
  text-decoration: none;
  cursor: pointer;
  font: inherit;
  font-size: 0.75rem;
  padding: 0.75rem 1.5rem; /* Увеличенные отступы */
  display: block;
  transition: all 0.3s ease; /* Плавная анимация */
  border-radius: 25px; /* Овальная форма */
  margin: 5px auto;
}

.menu_link:hover,
.menu_link:focus-visible {
  outline-offset: 2px; /* Отступ рамки от текста */
  background: rgba(255, 255, 255, 0.1); /* Полупрозрачный фон */
  color: orange; /* Желтый цвет для акцента */
  transform: scale(1.02); /* Легкое увеличение */
}

.menu_link.selected {
  background: rgba(255, 255, 255, 0.1); /* Фон для выбранного пункта */
  color: orange; /* Желтый цвет для выделения */
  font-weight: bold; /* Жирный шрифт */
}
