:root {
  --dark-bg: #0a0a0a;
  --card-bg: #0f0f0f;
  --accent-color: #ffa500;
  --text-color: #ffffff;
  --input-bg: #1c1c1c;
  --error-color: #ff4444;
  --hover-color: #ff8c00;
  --glow-speed: 2s;
}

.containerLogin {
  padding: 12px 60px 12px 60px;
  background-color: #1D1D1D;
}

.login-container {
  height: 75vh;
  display: flex;
  align-items: center;
  justify-content: center;
  /*background: linear-gradient(135deg, #0a0a0a, #101010);*/
  padding: 120px 0 0 0;
  overflow: hidden;
}

.login-card {
  background: var(--card-bg);
  border-radius: 16px;
  padding: 30px 40px;
  width: 100%;
  max-width: 420px;
  border: 2px solid var(--accent-color);
  position: relative;
  animation: glow var(--glow-speed) ease-in-out infinite;
}


@keyframes glow {
  0%, 100% { box-shadow: 0 0 45px rgba(255, 165, 0, 0.5), 0 0 15px rgba(255, 165, 0, 0.2) inset; }
  50% { box-shadow: 0 0 5px rgba(255, 165, 0, 0.3), 0 0 15px rgba(255, 165, 0, 0.3) inset; }
}

.login-header {
  text-align: center;
  margin-bottom: 30px;
}

.login-title {
  font-size: 1.8rem;
  color: var(--accent-color);
  font-weight: 600;
  margin-bottom: 10px;
  letter-spacing: 1px;
  text-shadow: 
    0 0 10px rgba(255, 165, 0, 0.7),
    0 0 20px rgba(255, 165, 0, 0.5);
}

.login-subtitle {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.95rem;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-label {
  margin-bottom: 8px;
  color: var(--text-color);
  font-size: 0.9rem;
  font-weight: 500;
}

.form-input {
  background: var(--input-bg);
  border: 1px solid rgba(255, 165, 0, 0.3);
  padding: 12px 16px;
  border-radius: 8px;
  color: var(--text-color);
  font-size: 1rem;
  transition: all 0.3s ease;
  outline: none;
  position: relative;
  z-index: 1;
}

.form-input::placeholder {
  color: rgba(255, 255, 255, 0.3);
}

.form-input:focus {
  border-color: var(--accent-color);
  box-shadow: 0 0 10px rgba(255, 165, 0, 0.5);
  z-index: 2;
}

.login-button {
  background: linear-gradient(to right, var(--accent-color), var(--hover-color));
  color: #000000;
  padding: 12px 20px;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.login-button::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 300%;
  height: 300%;
  background: radial-gradient(circle, rgba(255, 165, 0, 0.3) 0%, transparent 70%);
  transform: translate(-50%, -50%) scale(0);
  transition: transform 0.3s ease;
  border-radius: 50%;
  z-index: 0;
}

.login-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 0 15px rgba(255, 165, 0, 0.6);
}

.login-button:hover::before {
  transform: translate(-50%, -50%) scale(1);
  z-index: 0;
}

.spinner {
  background: var(--accent-color);
  color: #000000;
  padding: 12px 20px;
  border-radius: 8px;
  text-align: center;
  font-weight: 600;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% { opacity: 0.4; }
  50% { opacity: 1; }
  100% { opacity: 0.4; }
}

/* Ошибки валидации */
.error-message {
  color: var(--error-color);
  font-size: 0.85rem;
  margin-top: 5px;
  display: block;
}

.register-link {
  text-align: center;
  margin-top: 20px;
}

.register-button {
  background: transparent;
  border: 2px solid #ffa500;
  color: #ffa500;
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-block;
}

.register-button:hover {
  background: #ffa500;
  color: #000000;
  box-shadow: 0 0 10px rgba(255, 165, 0, 0.5);
}