/* Заголовок */
.title {
  text-align: start;
  margin-bottom: 10vh;
}

.title_text {
  padding: 5vh 0 0 0;
  font-size: 3.5rem;
  text-transform: uppercase;
  background: linear-gradient(
    90deg,
    var(--white),
    var(--secondary-gold),
    var(--primary-gold)
  );
  -webkit-background-clip: text; /* Для поддержки градиента */
  -webkit-text-fill-color: transparent; /* Для поддержки градиента */
}

/* Общий контейнер */
.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.block {
  width: 21vw;
  height: 41vh;
  border: 1px solid var(--secondary-gold);
  border-radius: 25px;
  background-color: #222222;
  align-self: flex-start;
  padding: 10px;
}

.block_text {
  margin: 0;
  padding: 10px;
  font-size: 1.5rem; /* Увеличиваем размер шрифта */
  text-align: center; /* Горизонтальное выравнивание */
  display: flex; /* Используем flexbox для центрирования */
  align-items: center; /* Вертикальное выравнивание */
  justify-content: center; /* Горизонтальное выравнивание */
  height: 100%; /* Занимает всю высоту родительского блока */
  background: linear-gradient(
    90deg,
    #fffefa,
    #fff7d5,
    #f0e68c
  ); /* Золотой градиент */
  -webkit-background-clip: text; /* Применяем градиент к тексту */
  background-clip: text;
  color: transparent; /* Скрываем основной цвет текста */
  font-weight: bold; /* Добавляем жирность */
}

.safety_1 {
  transform: translateX(7vw) translateY(0vh);
}

.safety_2 {
  transform: translateX(0vw) translateY(30vh);
}

.safety_3 {
  transform: translateX(-7vw) translateY(0vh);
}

.safety_4 {
  transform: translateX(-13.5vw) translateY(30vh);
}
