/* Обертка для графика */
.chartContainer {
  position: relative;
  width: 900px; /* Ширина графика */
  height: 400px; /* Высота графика */
}

/* Стиль для текстового блока */
.textBlock {
  position: absolute;
  top: -2.7em; /* Расстояние от верхнего края графика */
  right: 8em;
  transform: translateX(0%); /* Коррекция центрирования */
  background: linear-gradient(to right, #1a1a1a, #4d4d4d);
  color: #ffd700;
  font-size: 12px;
  font-weight: bold;
  padding: 8px 16px;
  border-radius: 5px;
  transition: transform 0.3s ease-in-out;
}

.textBlock:hover {
  transform: translate(0%, -3px) scale(1.05); /* Поднимаем и немного увеличиваем */
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3); /* Добавляем тень */
  color: #ffcc00; /* Меняем цвет текста */
}
