/* ========================================================================== */
/* Базовые стили страницы и анимации фона */
/* ========================================================================== */
html,
body {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family:
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", 
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--background-color);
}

code {
  font-family:
    source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.span-check {
  line-height: 1.3 !important;
}

.user-image {
  width: 30px;
  height: 30px;
}

.main-conteiner {
  display: flex;
  flex-direction: column;
  gap: 20px; /* Расстояние между секциями */
}

/* ========================================================================== */
/* Стили для scrollbar */
/* ========================================================================== */

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--background-color);
  /* Легкое свечение трека только с золотым оттенком */
  box-shadow:
    inset 0 0 5px rgba(194, 169, 88, 0.2),
    inset 0 0 8px rgba(194, 169, 88, 0.1);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  /* Градиент только с золотыми оттенками */
  background: linear-gradient(
    180deg,
    rgba(194, 169, 88, 0.2) 0%,
    /* Прозрачный золотой сверху */ rgba(194, 169, 88, 0.8) 30%,
    /* Золотой */ rgba(194, 169, 88, 1) 50%,
    /* Яркий золотой в центре */ rgba(194, 169, 88, 0.8) 70%,
    /* Золотой */ rgba(194, 169, 88, 0.2) 100% /* Прозрачный золотой снизу */
  );
  background-size: 100% 200%; /* Увеличиваем размер фона для плавного перелива */
  background-position: 0% 0%; /* Начальная позиция */
  border-radius: 4px;

  /* Мягкое свечение только с золотым оттенком */
  box-shadow:
    0 0 8px rgba(194, 169, 88, 0.3),
    0 0 10px rgba(194, 169, 88, 0.2),
    0 0 12px rgba(194, 169, 88, 0.1);

  /* Плавная анимация перелива */
  animation: gradientFlow 6s ease-in-out infinite;
  will-change: background-position;
}

::-webkit-scrollbar-thumb:hover {
  /* Усиливаем золотые оттенки при наведении */
  background: linear-gradient(
    180deg,
    rgba(194, 169, 88, 0.4) 0%,
    /* Прозрачный золотой */ rgba(194, 169, 88, 1) 30%,
    /* Яркий золотой */ rgba(194, 169, 88, 1) 50%,
    /* Яркий золотой в центре */ rgba(194, 169, 88, 1) 70%,
    /* Яркий золотой */ rgba(194, 169, 88, 0.4) 100% /* Прозрачный золотой */
  );
  background-size: 100% 200%;

  /* Усиливаем золотое свечение при наведении */
  box-shadow:
    0 0 10px rgba(194, 169, 88, 0.5),
    0 0 12px rgba(194, 169, 88, 0.4),
    0 0 14px rgba(194, 169, 88, 0.3);
}

@keyframes gradientFlow {
  0% {
    background-position: 0% 0%;
  }
  50% {
    background-position: 0% 100%;
  }
  100% {
    background-position: 0% 0%;
  }
}

/* ========================================================================== */
/* Переменные для стилей меню и общие настройки */
/* ========================================================================== */
:root {
  --primary-gold: #f3d58a; /* Основной золотой цвет */
  --secondary-gold: #d4af37; /* Более светлый золотой для градиентов */
  --white: #ffffff; /* Белый цвет */
  --black: #000000; /* Черный цвет */
  --shadow-dark: rgba(0, 0, 0, 0.5); /* Темная тень */
  --shadow-light: rgba(0, 0, 0, 0.3); /* Светлая тень */
  --primary-color: #7c6b33; /* Темный золотой */
  --secondary-color: #c2a958; /* Светлый золотой */
  --background-color: rgb(59, 59, 59); /* Фон */
  --text-color: #e0e0e0; /* Светло-серый текст */
  --hover-color: #ffffff; /* Светлый для ховер-эффекта */
  --success-color: #4caf50; /* Зеленый для ассоциаций с прибылью */
  --ai-accent-color: #00bcd4; /* Бирюзовый для ассоциаций с ИИ */
  --text-light-yellow: #ffffff; /* Светло-желтый цвет */
  /* Переменные для меню */
  --menu-closed-width: 12vw;
  --menu-closed-height: 6vh;
  --menu-open-width: 30vw;
  --menu-open-height: 75vh;
  --menu-transition: all 0.1s ease-in-out;

  --padding: 1rem;
  --button-padding: 0.625rem;
  --menu-top-offset: 1rem;
  --border-radius: 8px;
  --box-shadow:
    0 0 8px rgba(0, 0, 0, 0.6), 0 0 12px rgba(124, 107, 51, 0.25),
    0 0 16px rgba(194, 169, 88, 0.15), 0 0 20px rgba(85, 70, 150, 0.08),
    0 0 24px rgba(76, 175, 80, 0.08); /* Добавлен легкий зеленый оттенок */
  --transition: all 0.3s ease;
}

/* ========================================================================== */
/* Стили для верхней тёмной полосы */
/* ========================================================================== */

.top-line {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%; /* Полная ширина экрана */
  height: 5vh; /* Высота полосы */
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0),
    /* Полностью прозрачный снизу */ rgba(0, 0, 0, 0.05),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.1),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.15),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.2),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.25),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.3),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.35),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.4),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.45),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.5),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.55),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.6),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.65),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.7),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.8),
    /* Промежуточный слой */ rgba(0, 0, 0, 0.9),
    /* Промежуточный слой */ rgba(0, 0, 0, 1) /* Последний слой, непрозрачный */
  );
  z-index: 99; /* Устанавливаем z-index ниже, чем у переключателя (z-index: 100) */
  pointer-events: none; /* Чтобы полоса не мешала взаимодействию с элементами под ней */
}

/* ========================================================================== */
/* Стили секций */
/* ========================================================================== */

section {
  width: 100%;
  min-height: 100vh;
  margin: auto;
  padding: 0;
  box-sizing: border-box;
}

#home {
  padding-top: clamp(
    20px,
    5vw,
    5vw
  ) !important; /* Увеличиваем отступ сверху для десктопов(20px для мобильных устройств 7vw для планшетов 80px для персональных компьютеров) */
}

/* ========================================================================== */
/* Стили для меню и его компонентов */
/* ========================================================================== */

.header-container {
  position: fixed;
  right: 1vw;
  top: 3vh;
  z-index: 1000;
}

/* Оверлей при открытом меню */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  pointer-events: none; /* Отключаем события мыши для оверлея */
}

/* Стили меню */
.menu {
  position: relative;
  width: var(--menu-closed-width);
  height: var(--menu-closed-height);
  background: transparent;
  /* Прямоугольник в закрытом состоянии */

  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  cursor: pointer;
  pointer-events: auto;
  transition:
    width 0.35s ease-in-out,
    height 0.3s ease-in-out,
    clip-path 0.4s ease-in-out;
}

.menu.open {
  width: var(--menu-open-width);
  height: var(--menu-open-height);
  background: linear-gradient(
    135deg,
    var(--primary-gold),
    var(--secondary-gold)
  );
  border-radius: 5px;
  /*clip-path: polygon(0 0, 100% 10%, 100% 90%, 0 100%); /* Трапеция в открытом состоянии */
  border: none;
  z-index: 1000;
}

/* Псевдоэлемент для анимированной границы в закрытом состоянии */
.menu:not(.open)::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -3px;
  right: -3px;
  bottom: -3px;
  border-radius: 15px;
  border: 2px solid var(--ai-accent-color);
  box-shadow:
    0 0 10px rgba(0, 188, 212, 0.5),
    /* Свечение */ 0 0 20px rgba(0, 188, 212, 0.3);
  background: linear-gradient(
    90deg,
    transparent 0%,
    transparent 40%,
    rgba(0, 188, 212, 0.1) 50%,
    transparent 60%,
    transparent 100%
  );
  background-size: 200% 100%;
  animation:
    scanBorder 3s linear infinite,
    pulseGlow 2.618s ease-in-out infinite; /* Добавляем анимацию свечения */
  opacity: 0.8;
  z-index: -1;
  will-change: background-position, box-shadow;
}
/* Ключевые кадры для анимации "сканирования" */
@keyframes scanBorder {
  0% {
    background-position: 0% 0%;
  }
  50% {
    background-position: 200% 0%;
  }
  100% {
    background-position: 400% 0%;
  }
}

/* Ключевые кадры для анимации свечения */
@keyframes pulseGlow {
  0% {
    box-shadow:
      0 0 10px rgba(0, 188, 212, 0.5),
      0 0 20px rgba(0, 188, 212, 0.3);
  }
  50% {
    box-shadow:
      0 0 15px rgba(0, 188, 212, 0.7),
      0 0 30px rgba(0, 188, 212, 0.5);
  }
  100% {
    box-shadow:
      0 0 10px rgba(0, 188, 212, 0.5),
      0 0 20px rgba(0, 188, 212, 0.3);
  }
}

/* Заголовок меню */
.menu-title {
  text-transform: uppercase;
  width: 100%;
  height: 100%;
  position: absolute;
  font-size: 1vw;
  color: var(--text-color);
  text-align: center;

  pointer-events: none; /* Отключаем события мыши для заголовка */
  /* Добавляем flex для центрирования */
  display: flex;
  justify-content: center; /* Центрирование по горизонтали */
  align-items: center; /* Центрирование по вертикали */
  /* Плавный переход для изменений позиции */
  transition: all 0.4s ease-in-out; /* Добавляем переход для всех свойств */
}

/* Стили для открытого состояния меню */
.menu.open .menu-title {
  font-size: 1.35vw;
  color: black;
  height: auto; /* Убираем фиксированную высоту */
  top: 19px; /* Отступ сверху для позиционирования в верхней части */
  left: 0;
  right: 0;
  align-items: flex-start; /* Переключаем выравнивание, чтобы текст был вверху */
  transform: none; /* Убираем смещение, чтобы текст прижимался к верхней части */
}

/* Пульсация заголовка в закрытом состоянии */
.menu:not(.open) .menu-title {
  align-items: flex-start; /* Переключаем выравнивание, чтобы текст был вверху */
  padding: 6.5% 0 0 0;
  letter-spacing: 0em; /* Устанавливаем увеличенное расстояние между буквами */
  animation: pulseTitle 2.6s ease-in-out infinite; /* Уменьшенная длительность */
}

/* Ключевые кадры для анимации пульсации */
@keyframes pulseTitle {
  0%,
  20%,
  40%,
  60%,
  80%,
  100% {
    transform: scale(1);
    color: var(--text-color);
  }
  10% {
    transform: scale(1.03); /* Уменьшенная амплитуда */
    color: rgba(194, 169, 88, 0.5); /* Золотой оттенок с прозрачностью */
  }
  30% {
    transform: scale(1.03);
    color: rgba(194, 169, 88, 0.5); /* Золотой оттенок с прозрачностью */
  }
}

/* Анимация пульсации для закрытого меню */
.menu:not(.open) {
  animation: pulse 2.618s ease-in-out infinite;
}

/* Ключевые кадры для анимации пульсации */
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

/* Список пунктов меню */
.menu-items {
  list-style: none;
  padding: 0;
  margin: 60px 0 0 0;
  opacity: 0;
  visibility: hidden;
  transition: var(--menu-transition);
}

.menu.open .menu-items {
  opacity: 1;
  visibility: visible;
}

/* Добавляем пунктирную линию */
.dotted-line {
