  /*background-size: 6px 1px; /* Размер пунктира: 6px — длина штриха, 1px — высота */
}

/* Пункты меню */
.menu-item {
  margin: 0;
  position: relative;
  color: var(--text-color);
  font-size: 1.2rem;
  padding: 10px 20px 0 20зч;
  cursor: pointer;
  transition: color 0.3s ease;
}

/* Добавляем пунктирную линию под каждым пунктом меню */
.menu-item:first-child::before {
  content: "";
  display: block;
  width: 90%;
  margin: 1vh auto;
  height: 1px;
  background: linear-gradient(to right, rgb(0, 0, 0, 0.9) 50%, transparent 50%)
    repeat-x;
  background-size: 6px 1px;
}

.menu-item::after {
  content: "";
  display: block;
  width: 90%;
  margin: 1vh auto;
  height: 1px;
  background: linear-gradient(to right, rgb(0, 0, 0, 0.9) 50%, transparent 50%)
    repeat-x;
  background-size: 6px 1px;
}

.menu-link {
  display: flex;
  align-items: center;
  color: black;
  text-transform: uppercase;
  text-decoration: none;
  padding: 0.75vh 2vw;
  border-radius: 5px;
  position: relative; /* Для правильного позиционирования псевдоэлементов */
}

/* Псевдоэлемент для анимации фона */
.menu-link::before {
  content: ""; /* Пустой контент, так как это просто фон */
  position: absolute;
  top: 0;
  left: 1.5vw; /* Исправлено: начинаем с левого края */
  width: 0; /* Изначально ширина 0 */
  height: 100%; /* Высота равна высоте ссылки */
  background: rgba(0, 0, 0, 1); /* Цвет фона */
  border-radius: 5px;
  transition: width 0.5s ease; /* Плавное изменение ширины */
  z-index: -1; /* Помещаем фон под текст */
}

/* Эффект при наведении для фона */
.menu-link:hover::before {
  width: 87%; /* При наведении ширина становится 100% */
}

/* Стили для текста в меню */
.menu-item-text {
  position: relative;
  display: inline-block;
  transition: color 0.3s ease;
  font-size: 2rem;
  color: black; /* Исходный цвет текста */
}

.menu-item-text::before {
  /*content: attr(data-text);*/
  position: absolute;
  top: 0;
  left: 0;
  color: var(--ai-accent-color); /* Бирюзовый цвет для нулей и единиц */
  overflow: hidden;
  clip-path: polygon(0 0, 0 0, 0 100%, 0 100%);
  transition: clip-path 0.6s ease-in-out;
  z-index: 1;
}

.menu-item:hover .menu-item-text::before {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}

.menu-item-text::after {
  /*content: attr(data-text);*/
  position: relative;
  z-index: 2;
}

/* Эффект изменения цвета текста с помощью градиента */
.menu-link:hover .menu-item-text {
  color: transparent; /* Делаем текст прозрачным, чтобы показать градиент */
  background: linear-gradient(
    to left,
    rgb(0, 0, 0) 50%,
    rgb(255, 255, 255) 50%
  ); /* Градиент из зелёного в чёрный */
  background-size: 200% 100%; /* Увеличиваем размер фона для анимации */
  background-position: 100% 0; /* Смещаем фон вправо */
  -webkit-background-clip: text; /* Обрезаем фон по тексту (для WebKit-браузеров) */
  background-clip: text; /* Обрезаем фон по тексту */
  animation: textColorSlide 0.5s ease forwards; /* Запускаем анимацию */
}

/* Анимация для изменения цвета текста */
@keyframes textColorSlide {
  0% {
    background-position: 100% 0; /* Начальное положение: чёрный цвет */
  }
  100% {
    background-position: 0 0; /* Конечное положение: зелёный цвет */
  }
}

