
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); /* затемнение */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.modal {
  display: grid;
  position: relative;
  background: #0F0F0F;
  border: 2px solid white;
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(0,0,0,0.2);
  width: 716px;
  height: auto;
  text-align: center;
  padding: 44px 48px 48px 48px;
  gap: 28px;
}

.title {
  text-align: start;
  font-family: 'Roboto';
  font-size: 24px;
  font-weight: 600;
  line-height: 100%;
  color: #FFFFFF;
  margin: 0;
}

.description {
  display: block;
  width: auto;
  text-align: start;
  color: #FFFFFF;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  letter-spacing: -1.9%;
}


.buttonBlock {
  display: flex;
  justify-content: flex-start; 
  gap: 10px; 
}

.modal input {
  width: 460px;
  height: 48px;
  font-size: 16px;
  font-weight: 400;
  font-family: SF Pro;
  padding: 0 20px;
  border-radius: 20px;
  border: 1px solid #FFFFFF;
  background-color: #000000;
  color: #D9D9D9;
  font-size: 16px;
}

/* Общие стили для кнопок */
.createButton,
.doneButton {
  position: relative;
  width: 305px;
  height: 48px;
  font-size: 16px;
  font-weight: 590;
  line-height: 150%;
  text-align: start;
  border-radius: 16px;
  padding: 0 12px;
  border: 1px solid white;
  cursor: pointer;
  overflow: hidden;
  transition: background-color 0.5s ease, color 0.5s ease;
}

/* Стили для createButton */
.createButton {
  background-color: #147AFF;
  color: white;
}

/* Градиентный фон, появляющийся при наведении */
.createButton::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 16px;
  background: linear-gradient(135deg, #413988, #CFDCF9, #231C83);
  opacity: 0;
  transition: opacity 0.5s ease;
  z-index: 0;
}

/* Текст на кнопке должен быть поверх градиента */
.createButton span {
  position: relative;
  z-index: 1;
}

/* При наведении плавно показываем градиент */
.createButton:hover::before {
  opacity: 1;
}

/* При наведении — цвет текста */
.createButton:hover {
  color: white;
}

/* Стили для doneButton */
.doneButton {
  background-color: #07061B;
  color: #147AFF;
}

/* Hover-эффект для doneButton */
.doneButton:hover {
  background-color: #147AFF;
  color: #EDEDED;
}



.closeButton {
  position: absolute;
  top: 7px;
  right: 10px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 4px;
  width: 42px;
  height: 42px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.2s ease;
}

.closeButton:hover {
  color: #ffffffb2;
}