.container {
  border: 2px solid white;
  border-radius: 20px;
  margin-bottom: 10px;
}

.header {
  display: flex;
  justify-content: space-between;
	align-items: center;
  margin: 20px;
}

.tytle {
  color: #FFFFFF;
  font-size: 32px;
  line-height: 150%;
  margin: 0;
}

.ideas {
  margin: 0 20px 20px 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
 }

.ideasCard {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
  width: 200px;
  min-height: 180px;
  border-radius: 20px;
  border: 2px solid white;
  background-color: #1D1D1D; /* для фона */
}

.ideasTitle {
  color: #FFFFFF;
  font-size: 24px;
  font-weight: 600;
  margin: 0 0 10px 0;
}

.ideasDescription {
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 400;
  flex-grow: 1; /* растягиваем */
}

.ideasDetails {
  background-color: transparent;
  width: 100%;
  height: 54px;
  color: #147AFF;

  border-radius: 20px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  margin-top: auto;
  transition: background-color 0.2s;
}

.ideasDetails:hover {
  background-color: #147aff22;
}

.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal {
  background-color: #2A2A2A;
  color: white;
  padding: 30px;
  border-radius: 12px;
  max-width: 400px;
  width: 90%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.8);
  text-align: left;
}

.closeButton {
  margin-top: 20px;
  background-color: #444;
  color: white;
  border: none;
  padding: 10px 16px;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.closeButton:hover {
  background-color: #555;
}