.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  border-top: 2px solid white;
  margin-top: 25px;
  background-color: #1D1D1D;
}

.banner {
  display: flex;
  justify-content: space-between;
}

.logo { 

  color: #FFFFFF;
  font-size: 32px;
  line-height: 150%;
  letter-spacing: -1.9%;
  text-align: start;
  margin: 0;
}

.socialMedia {
  display: flex;
  margin-top: 10px;
}

.instagram {
  margin-right: 25px;
}

.whatsapp {


}

.mobile {
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  letter-spacing: -1.9%;
  margin-bottom: 10px;
  color: #FFFFFF;
}