.toggleTabs {
  display: flex;
  outline: 2px solid white;
  gap: 12px;
  padding: 4px;
  background-color: #1D1D1D;
  border-radius: 16px;
  margin: 0;
}

.tabButton {
  padding: 8px 16px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  font-weight: 500;
  border-radius: 4px;
  transition: background-color 0.2s, color 0.2s;
  color: rgba(255, 255, 255, 0.329);
}

.tabButton:hover {
  color: white;
}

.active {
  color: white;
  outline: 2px solid white;
  border-radius: 12px;
}