.container {
  border: 2px solid white;
  border-radius: 20px;
  margin-bottom: 10px;
}

.header {
  display: flex;
  justify-content: space-between;
	align-items: center;
  margin: 20px;
}

.tytle {
  color: #FFFFFF;
  font-size: 32px;
  line-height: 150%;
  margin: 0;
}

.creation {
  display: flex;
  flex-wrap: nowrap;
	justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin: 20px;
}

.period {
  display: flex;
  align-items: center;
}

.label {
  color: #fff;
  font-size: 20px;
  font-weight: 500;
  line-height: 150%;
  letter-spacing: -1.9%;
  margin-right: 18px;
}

