.look,
.download {
  background-color: transparent;
  height: 54px;
  color: #147AFF;
  padding: 12px 20px;
  border-radius: 20px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  margin-top: auto;
  margin-left: 18px;
  transition: background-color 0.2s;
}

.look:hover,
.download:hover {
  background-color: #147aff22;
}

/* Модальное окно */
.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background-color: #2A2A2A;
  color: white;
  padding: 30px;
  border-radius: 12px;
  width: 300px;
  text-align: center;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.6);
}

.closeButton {
  margin-top: 20px;
  background-color: #444;
  color: white;
  border: none;
  padding: 10px 16px;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.closeButton:hover {
  background-color: #666;
}