.container {
  width: auto;

}

.title {
  display: block;
  width: 100%;
  text-align: center;
  margin: auto;
  font-size: 20px;
  font-weight: 500;
  line-height: 150%;
  letter-spacing: -1.9%;
  color: #FFFFFF;
}

.performanceLight {
  display: flex;
  justify-content: center;
	align-items: center;
  height: 200px;
  width: 200px;
  border-radius: 100px;
  border: 2px solid white;
  margin: 16px 0;
}

.value {
  font-size: 40px;
  font-weight: 500;
  line-height: 150%;
  letter-spacing: -0.5%;
  color: #FFC11C;
}