.tool_card {
  border: 1px solid rgba(255, 165, 0, 0.25);
  border-radius: 8px;
  padding: 16px;
  width: 85%;
  height: 100%;
  background-color: #2d3a3a3b;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  /*background: linear-gradient(
    135deg,
    rgba(255, 243, 224, 0.9),
    rgba(255, 236, 179, 0.8),
    rgba(255, 243, 224, 0.9)
  );*/
  transition:
    transform 0.3s ease,
    box-shadow 0.3s ease; /* Плавные переходы */
  margin: 0 0 0 0;
}

.tool_title {
  color: #ffffff;
  font-weight: 100;
  font-size: 2em;
  margin: 20px 0 25px 30px;
}

.pie_chart {
  display: flex;
  flex-direction: row;

  margin: 0 0 0 5%;
}

.deposit_distribution_title {
  display: flex;
  font-size: 1em;
  color: #d6d6d6;
  list-style: none;
  padding: 2px;
  margin: 0 0 0 0;
  cursor: pointer;
}

.deposit_distribution_title:hover {
  transform: scale(1.07); /* Увеличиваем размер на 7% */
}

.deposit_distribution_color {
  width: 30px;
  height: 2px;
  margin: auto 20px;
  border-radius: 2px;
}

.recharts-sector {
  transition: transform 0.3s ease; /* Плавный переход */
}

.recharts-sector:hover {
  transform: scale(1.1); /* Увеличиваем размер на 10% */
}

.sector {
  transition: transform 0.3s ease;
}

.sector:hover {
  transform: scale(1.1);
}

.fadeText {
  transition:
    opacity 1.3s ease,
    transform 0.3s ease;
  opacity: 0;
  transform: scale(0.95);
}

.fadeTextVisible {
  opacity: 1;
  transform: scale(1);
}

.text {
  font-size: 1.8em;
  fill: #ffffff;
  font-weight: 500;
  transition:
    transform 0.4s ease,
    opacity 0.4s ease;
  opacity: 1;
}

.textEnter {
  opacity: 0;
  transform: translateX(30px);
  animation: enter 0.3s forwards ease;
}

.textExit {
  opacity: 1;
  transform: translateX(0);
  animation: exit 0.1s forwards ease;
}

@keyframes enter {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes exit {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-30px);
  }
}
