.container {
  display: flex;
  width: 100%;
  position: relative;
}

.chart_info {
  display: flex;
  justify-content: space-between;
  align-items: center; 
  width: 100%; 
  position: absolute;

  padding: 0.2em 0.2em; 
  box-sizing: border-box; 
}

.pair_title {
  display: flex;
  flex-direction: column;
  color: white;
  padding: 0.2em 0.2em; 
  z-index: 1;
}

.pair_info {
  display: flex;
  flex-direction: column;
  color: white;
  padding: 0.2em 0.9em; 
  z-index: 1;
}

.pair_name {
  font-size: 12px; 
}

.pair_value {
  font-size: 24px;
  font-weight: 800;
}

.pair_indicator,
.pair_high_rate, 
.pair_low_rate {
  width: auto;
  font-size: 12px;
  text-align: right;
  white-space: nowrap;
  align-self: flex-end;
  padding: 0.1em 0.2em;
}

.rate_active {
  text-align: right;

  padding: 0.1em 0.2em;
  border-radius: 3px;

  box-shadow: 0 0 6px orange, 0 0 1em orange;
  background: orange;
}