.items_wrap {
  width: 100%;
  height: 40px;
  display: flex;
  overflow: hidden;
  user-select: none;
  border-top: 0.5px solid rgba(255, 238, 0, 0.25); /* Полупрозрачная граница сверху */
  border-bottom: 0.5px solid rgba(255, 238, 0, 0.25); /* Полупрозрачная граница снизу */
  border-left: none; /* Убираем левую границу */
  border-right: none; /* Убираем правую границу */
  background-color: #222222;
}

.ticker {
  height: 40px;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.content {
  display: flex;
  white-space: nowrap;
  animation: scroll-left 5s linear infinite;
}

.item {
  display: flex;
  align-items: center;
  margin: auto;
  padding: 7px 0;
  color: #00ffff;
  font-size: clamp(12px, 1.2vw, 16px);
  min-width: auto;
}

.symbol {
  color: #f8f8f8;
  font-weight: bold;
  margin-right: 15px;
}

.price {
  color: rgb(8, 153, 129);
}

.separator {
  display: flex; /* Используем Flexbox для центрирования */
  align-items: center; /* Центрирование по вертикали */
  justify-content: center; /* Центрирование по горизонтали */
  width: 100%; /* Занимает всю доступную ширину */
  height: 100%; /* Занимает всю доступную высоту */
  color: #ffee00;
  opacity: 0.25;
  margin: 0 5px;
}

.separator::after {
  content: ""; /* Создаем псевдоэлемент для полосы */
  display: block;
  width: 1px; /* Толщина полосы */
  height: 60%; /* Высота полосы */
  background-color: #ffee00; /* Цвет полосы */
}

/* Анимация для бесшовной прокрутки */
@keyframes scroll-left {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

.items {
  flex-shrink: 0;
  display: flex;

  counter-reset: item;
  justify-content: space-around;
  min-width: 100%;
}
.item {
  flex: 0 0 auto;
  width: 295px;

  border-radius: 6px;
  display: flex;
  justify-content: start;
  align-items: center;
  font-size: 1rem;
  color: #dbdbdb;
  margin: 10px;
  transition: all 0.1s ease-in-out;
}
.item:hover {
  transform: scale(1.05);
}

/*.item:before {
  content: counter(item);
}*/

.perfscan {
  margin: 20px 0;
  text-align: center;
  bottom: 0;
  background: #fff;
  padding: 5px;
}
.perfscan hr {
  border: solid #999;
  border-width: 1px 0 0 0;
  max-width: 50%;
  margin: 0 auto 20px;
}
.perfscan a {
  color: #000;
  font-weight: bold;
}

/* Останавливаем анимацию при наведении */
.items_wrap:hover .marquee {
  animation-play-state: paused;
}

.price {
  color: #dbdbdb; /* Белый цвет по умолчанию */
  transition: color 0.25s ease-in-out;
}

.change {
  color: #dbdbdb; /* Белый цвет по умолчанию */
  transition: color 0.25s ease-in-out;
  margin-left: 18px;
  font-size: 0.9rem;
}

.percentageChange {
  color: #dbdbdb; /* Белый цвет по умолчанию */
  transition: color 0.25s ease-in-out;
  margin-left: 5px;
  font-size: 0.9rem;
}

.green {
  color: rgb(8, 153, 129); /* Зелёный цвет для роста */
  transition: color 0.25s ease-in-out;
}

.red {
  color: rgb(255, 0, 0); /* Красный цвет для падения */
  transition: color 0.25s ease-in-out;
}

.icon {
  margin-right: 5px;
}
