.container{
  display: flex;
  flex-direction: column;
  border: 2px solid white;
  border-radius: 20px;
  margin-bottom: 10px;
}

.header{
  display: flex;
  justify-content: space-between;
	align-items: center;
  margin: 20px;
}


.tytle{
  color: #FFFFFF;
  font-size: 32px;
  font-weight: 500;
  line-height: 150%;
  margin: 0;
}

.strategy{
  display: flex;
  margin: 20px;
}

.strategyIcon{

}

.strategyInformation{
  width: 100%;
  color: #FFFFFF;
  font-size: 32px;
  line-height: 150%;
  margin: 0 0 0 20px;
}

.strategyHeader{
  display: flex;
  height: 54px;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 18px;
}

.strategyTitle{
  color: #FFFFFF;
  font-size: 24px;
  line-height: 100%;
  letter-spacing: 1%;
  vertical-align: bottom;
  margin: 0;
}

.strategyChange{
  background-color: transparent;
  width: 172px;
  height: 54px;
  color:#147AFF;
  padding: 12px 30px 12px 5px;
  border-radius: 20px;
  font-size: 20px;
  font-weight: 500;
  line-height: 150%;
  letter-spacing: -1.9%;
}

.strategyChange:hover {
  background-color: #147aff22;
}

.strategyProfitability{
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.strategyDescription{
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  margin: 0;
}

.strategyRisk{
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  margin: 0;
}

.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6); /* затемнение */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal {
  background-color: #2A2A2A; /* фон модалки */
  color: #ffffff; /* текст белый */
  padding: 20px;
  border-radius: 10px;
  width: 320px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.7);
}

.strategyList {
  list-style: none;
  padding: 0;
  margin: 0;
}

.strategyItem {
  padding: 12px;
  border-bottom: 1px solid #3A3A3A;
  cursor: pointer;
  transition: background-color 0.2s;
}

.strategyItem:hover {
  background-color: #3A3A3A;
}

.closeButton {
  margin-top: 15px;
  background-color: #444;
  color: #fff;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.closeButton:hover {
  background-color: #555;
}