.container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border: 2px solid white;
  border-radius: 20px;
  margin-bottom: 10px;
  padding: 0 28px;
}

.header{
  display: flex;
  flex-direction: column;

  margin: 20px;
}


.tytle{
  color: #FFFFFF;
  font-size: 32px;
  line-height: 150%;
  font-weight: 500;
  margin: 0;
}

.workTime {
  color: #FFFFFF;
  font-size: 24px;
  font-weight: 600;
  line-height: 100%;
  margin: 0;
}

.status {
  align-self: center;
}

.statusChange{
  background-color: transparent;
  width: 218px;
  height: 54px;
  color:#147AFF;
  padding: 12px 20px;
  border-radius: 20px;
  font-size: 20px;
  font-weight: 500;
  line-height: 150%;
  letter-spacing: -1.9%;
  text-align: start;
}

.statusChange:hover {
  background-color: #147aff22;
}

