/* ========================================================================== */
/* Эффект сдвига */
/* ========================================================================== */

/* Эффект затемнения */
.block--1,
.block--2 {
  transition: all 1s ease; /* Плавные переходы для всех изменений */
}

/* ========================================================================== */
/* Основные стили */
/* ========================================================================== */

.blocks-container--1-2 {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.block--1 {
  width: 65vw;
  height: auto;
}

.block--2 {
  width: 65%;
  height: auto;
  align-self: flex-end;
}

.blocks-container--3-4 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 10vh;
}

.block--3 {
  width: 25vw;
  height: auto;
  align-self: flex-start;
}

.block--4 {
  width: 65%;
  height: auto;
  align-self: flex-start;
}

.home {
  padding: 20px;
  line-height: 1.6;
}

.home-header {
  text-align: center;
  margin-bottom: 40px;
}

/* Стили для заголовка */
.home-text__title {
  display: inline;
  width: auto;
  font-weight: 400; /* Жирный шрифт для акцента */
  line-height: 1.2;
  margin: 0;
  padding: 0;
  letter-spacing: 0.05em; /* Легкий разряд для читаемости */
  text-transform: uppercase; /* Верхний регистр для акцента */
  color: #ffffff;
}

/* Экстра-большой заголовок (xl) */
.home-text__title--xl {
  font-size: 6vw;
  line-height: 1.2;
  background: linear-gradient(
    90deg,
    var(--white),
    var(--secondary-gold),
    var(--primary-gold)
  );
  -webkit-background-clip: text; /* Для поддержки градиента */
  -webkit-text-fill-color: transparent; /* Для поддержки градиента */
  transition: all 0.3s ease-in-out; /* Плавные переходы для всех изменений */
}

/* Эффекты при наведении */
.home-text__title--xl:hover {
  /* Изменение градиента при наведении */
  background: linear-gradient(
    90deg,
    var(--secondary-gold),
    var(--white),
    var(--primary-gold)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;

  /* Усиление тени при наведении */
  text-shadow:
    2px 2px 10px var(--secondary-gold),
    0 0 15px var(--primary-gold);

  /* Легкое масштабирование */
  transform: scale(1.05);

  /* Курсор указывает на интерактивность */
  cursor: pointer;
}

/* Большой заголовок (lg) */
.home-text__title--lg {
  font-size: 3.5vw;
  line-height: 1.5;
  background: linear-gradient(
    90deg,
    var(--white),
    var(--secondary-gold),
    var(--primary-gold)
  );
  -webkit-background-clip: text; /* Для поддержки градиента */
  -webkit-text-fill-color: transparent; /* Для поддержки градиента */
  transition: all 0.3s ease-in-out; /* Плавные переходы для всех изменений */
}

/* Средний заголовок (md) */
.home-text__title--md {
  font-size: 2.5vw;
  line-height: 1.8;
}

/* Маленький заголовок (sm) */
.home-text__title--sm {
  font-size: clamp(
    1rem,
    3vw,
    1.5rem
  ); /* Мин: 16px, Предпочтительно: 3vw, Макс: 24px */
  line-height: 1.5;
}

/* Модификатор для адаптивного отступа сверху */
.home-text__title--mt {
  margin-top: clamp(
    10px,
    2vw,
    40px
  ); /* Адаптивный отступ сверху: Мин: 10px, Предпочтительно: 2vw, Макс: 40px */
}

/* Модификатор для центрирования текста */
.text-block--center {
  display: block;
  text-align: center;
  margin: 0 15%;
}

