.container {

  margin: 20px;

}

.title {
  height: 60px;
  padding: 10px 0;
  margin: 0;
  color: #FFFFFF;
  font-family: Roboto;
  font-weight: 500;
  font-size: 32px;
  line-height: 150%;
}

.buttonContainer {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  height: 58px;
  border-radius: 16px;
  padding: 4px;
  background: linear-gradient(154.17deg, #000000 27.17%, #FFFFFF 110.05%);
}

.buttonContainer::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  right: 2px;
  bottom: 2px;
  border-radius: 14px;
  background-color: #1D1D1D;
  z-index: 0;
}

.buttonContainer > * {
  position: relative;
  z-index: 1;
}

.buttonON,
.buttonOFF {
  position: relative;
  flex: 1;
  text-align: center;
  border: none;
  border-radius: 12px;
  padding: 12px 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 0;
  background: transparent; /* Убираем градиент по умолчанию */
}

/* Градиентная рамка — только для активных! */
.buttonON.active,
.buttonOFF.active {
  background: linear-gradient(160.57deg, #8C8C8C -32.35%, #FFFFFF -4.55%, #040404 70.91%);
}

/* Внутренний слой */
.buttonON::before,
.buttonOFF::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  right: 2px;
  bottom: 2px;
  border-radius: 10px;
  background-color: #1D1D1D;
  z-index: 1;
  transition: background-color 0.3s ease;
}

/* Текст */
.buttonON .buttonText,
.buttonOFF .buttonText {
  position: relative;
  z-index: 2;
}

/* Общий стиль текста */
.buttonText {
  font-size: 20px;
  font-weight: 500;
  line-height: 150%;
  letter-spacing: -0.5%;
  transition: color 0.3s ease, opacity 0.3s ease;
}

/* Активные состояния */
.buttonON.active::before {
  background-color: #20661C; /* Внутренний фон кнопки ВКЛ */
}

.buttonOFF.active::before {
  background-color: #1D1D1D; /* Можно оставить */
}

.active .buttonText {
  color: #FFFFFF;
  opacity: 1;
}

/* Неактивные состояния */
.inactive {
  background: transparent;
}

.buttonON.inactive .buttonText {
  color: #A0A0A0;
  opacity: 0.6;
}

.buttonOFF.inactive .buttonText {
  color: #FF4D4F;
  opacity: 0.8;
}