.header { 
  position: fixed;
  top: 0;
  width: 93.7%;
  height: 130px;
  margin: 0;
  padding: 0 0 0 0;
  z-index: 10;
  background-color: #1D1D1D;
}

.container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;

}

.companyIdentity {
  display: flex;
  align-items: center;
  gap: 32px;
}

.container .last-item {
  margin-left: auto; /* Прижимаем к правому краю */
}

.logo {
  height: 64px;
  width: 64px;
  border-radius: 50%;
  background: #D9D9D9;

}

.brandName{
  font-weight: 500;
  font-size: 40px;
  line-height: 150%;        
  letter-spacing: -1.9%;     
  text-align: center;
  color: #F4F4F4;

}

.userInformation {
  display: flex;
  border: 2px solid #FFC11C;
  border-radius: 20px;
  padding: 8px 12px;
}

.userPhoto {
  margin-right: 10px;
}

.userName {
  font-size: 24px;
  font-weight: 600;
  line-height: 100%;
  letter-spacing: 1%;
  color: #FFFFFF;
  margin: auto 0;
}

.mainMenu {
  display: flex;
  flex-direction: row;
	flex-wrap: nowrap;
	justify-content: space-between;
	align-items: center;
  height: 32px;
  border-top: 2px solid #FFFFFF;
  border-bottom: 2px solid #FFFFFF;
}

.navigation {
  display: flex;
  gap: 3rem;
  margin-right: 1rem;
}

.navLink {
  color: #147AFF;
  text-decoration: none;
  font-size: 1rem;
  transition: color 0.2s ease;
}

.navLink:hover {
  color: #a6ccff;
}

.loginButton{
  all: unset;
  cursor: pointer;
  height: 100%;
  font-weight: 500;
  font-size: 16px;
  color: #147AFF;
  line-height: 150%;
  letter-spacing: -1.9%;  
}

.loginButton:hover {
  color: #a6ccff;
}