:root {
  --dark-bg: #0a0a0a;
  --card-bg: #111111;
  --accent-color: #ffa500;
  --text-color: #ffffff;
  --input-bg: #1c1c1c;
  --error-color: #ff4444;
  --hover-color: #ff8c00;
}

.containerRegister {
  padding: 12px 60px 12px 60px;
  background-color: #1D1D1D;
}

.register-container {
  min-height: 75vh;
  display: flex;
  align-items: center;
  justify-content: center;
  /*background: linear-gradient(135deg, #0a0a0a, #101010);*/
  padding: 120px 0 0 0;
}

.register-card {
  background: #111111;
  border: 2px solid #ffa500;
  border-radius: 16px;
  padding: 30px 40px;
  box-shadow: 0 0 20px rgba(255, 165, 0, 0.3);
  max-width: 420px;
  width: 100%;
  text-align: center;
  animation: glow 2s ease-in-out infinite;
  position: relative;
}

@keyframes glow {
  0%, 100% {
    box-shadow: 0 0 10px rgba(255, 165, 0, 0.3), 0 0 20px rgba(255, 165, 0, 0.2) inset;
  }
  50% {
    box-shadow: 0 0 20px rgba(255, 165, 0, 0.5), 0 0 30px rgba(255, 165, 0, 0.3) inset;
  }
}

.register-header {
  text-align: center;
  margin-bottom: 30px;
}

.register-title {
  font-size: 1.8rem;
  color: #ffa500;
  margin-bottom: 10px;
  font-weight: 600;
  text-shadow: 0 0 10px rgba(255, 165, 0, 0.5);
}

.register-subtitle {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.95rem;
}

.register-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.register-form-group {
  display: flex;
  flex-direction: column;

}

.register-form-label {
  margin-bottom: 8px;
  color: #ffffff;
  font-size: 0.9rem;
  font-weight: 500;
  text-align: start; /* Центрируем текст меток */
  width: 100%; /* Метка занимает всю ширину поля */
}

.register-form-input {
  background: #1c1c1c;
  border: 1px solid rgba(255, 165, 0, 0.3);
  padding: 12px 16px;
  border-radius: 8px;
  color: #ffffff;
  font-size: 1rem;
  transition: all 0.3s ease;
  outline: none;
  position: relative;
}

.register-form-input:focus {
  border-color: #ffa500;
  box-shadow: 0 0 10px rgba(255, 165, 0, 0.5);
}

.register-button {
  background: linear-gradient(to right, #ffa500, #ff8c00);
  color: #000000;
  padding: 12px 20px;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.register-button::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 300%;
  height: 300%;
  background: radial-gradient(circle, rgba(255, 165, 0, 0.2) 0%, transparent 70%);
  transform: translate(-50%, -50%) scale(0);
  transition: transform 0.3s ease;
  border-radius: 50%;
}

.register-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 0 15px rgba(255, 165, 0, 0.6);
}

.register-button:hover::before {
  transform: translate(-50%, -50%) scale(1);
}

.spinner {
  background: #ffa500;
  color: #000000;
  padding: 12px 20px;
  border-radius: 8px;
  text-align: center;
  font-weight: 600;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% { opacity: 0.4; }
  50% { opacity: 1; }
  100% { opacity: 0.4; }
}

.login-link {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  margin-top: 20px;
}

.login-link a {
  color: #ffa500;
  text-decoration: none;
  font-weight: 600;
}

.login-link a:hover {
  text-decoration: underline;
}

.error-message {
  color: #ff4444;
  font-size: 0.85rem;
  margin-top: 5px;
  display: block;
}