.sidebar {
  width: 100%;
}

.menuList {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menuItem {
  box-sizing: border-box;
  width: 158px;
  padding: 16px 20px;
  cursor: pointer;
  color: #D9D9D9;
  border-radius: 20px;
  margin-bottom: 8px;

  position: relative;
  overflow: hidden;
  transition: color 0.4s ease;
}

.menuItem::before {
  content: "";
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background: linear-gradient(
    128.93deg,
    #1D1D1D 16.1%,
    #3D3D3D 32.58%,
    #1D1D1D 50.08%
  );
  opacity: 0;
  transition: opacity 0.4s ease;
  z-index: 0;
}

.menuItem:not(.active):hover::before {
  opacity: 1;
}

.menuItem:not(.active):hover {
  color: white;
}

.menuItem > * {
  position: relative;
  z-index: 1;
}


.icon {
  width: 60px;
  height: 60px;
  margin-top: 10px;
  border-radius: 50%;
}

.menuText {
  margin-top: 12px;
  font-size: 20px;
  font-weight: 500;
  line-height: 150%;
  letter-spacing: -.9px;
}

.active {
  background-color: #147AFF;
  color: white;
}