.container{
  border: 2px solid white;
  border-radius: 20px;
  margin-bottom: 10px;
}

.header{
  display: flex;
  justify-content: space-between;
	align-items: center;
  margin: 20px;
}


.tytle{
  color: #FFFFFF;
  font-size: 32px;
  font-weight: 500;
  line-height: 150%;
  margin: 0;
}

.inUse{
  color: #FFFFFF;
  font-size: 24px;
  line-height: 100%;
}

.depositInformation{
  margin: 0 0 20px 20px;

}

.depositCurrency{
  font-size: 24px;
  font-weight: 600;
  line-height: 100%;
  letter-spacing: 1%;
  color: #FFFFFF;
  margin: 0 0 20px 0;
}

.depositVolume{
  font-size: 40px;
  font-weight: 500;
  line-height: 150%;
  letter-spacing: -0.5%;
  color: #20661C;
  margin: 0 0 20px 0;
}

.profitInformation{
  display: flex;
  justify-content: space-between;
	align-items: center;
  width: 390px;
  border: 2px solid white;
  border-radius: 16px;
  margin: 0 20px;
  padding: 4px;
}

.profitToday{
  display: flex;
  justify-content: space-between;
  border: 2px solid white;
  border-radius: 12px;
  padding: 12px 20px;

}

.profitTodayTitle{
  font-size: 20px;
  font-weight: 500;
  line-height: 150%;
  letter-spacing: -0.5%;
  color: #FFFFFF;
  margin-right: 10px;
}

.profitTodayValue{
  margin: 0 0;
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  letter-spacing: -1.9%;
  color:#FFFFFF;
  background-color: #20661C;
}

.profitTotal{
  display: flex;
  justify-content: space-between;
  border: 2px solid white;
  border-radius: 12px;
  padding: 12px 20px;
}

.profitАmount{
  margin: 20px 20px;
  font-size: 40px;
  font-weight: 500;
  line-height: 150%;
  letter-spacing: -0.5%;
  color:#20661C
}

.active {
  border: 2px solid #FFFFFF;
  opacity: 1;
}

.inactive {
  border: 2px solid transparent;
  opacity: 0.5;
  cursor: pointer;
}

.active,
.inactive {
  transition: all 0.3s ease;
}

.profitToday:hover,
.profitTotal:hover {
  opacity: 0.8;
}

.statistics {
  display: flex;
  margin: 25px 25px;
  gap: 50px;

}
